























.siteInfo {
    background: white;
    padding: 20px;
    margin-top: 20px;
    width: 100%;
    box-sizing: border-box;
    box-shadow: 2px 2px 10px #cccccc;
    h3{
         border-bottom: 1px solid #cccccc;
         line-height: 40px;
         height: 40px;
         width: 100%;
         margin-bottom: 10px;
    }
    h3::after{
        display: block;
         content: '';
         width: 60px;
         background: #000;
         height: 2px;
         transition: all ease 2s;
    }
    h3:hover::after{
         display: block;
         content: '';
         width: 100px;
         background: #000;
         height: 2px;
    }
    p{
        line-height: 30px;
    }
}
