





















































































.home{
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.el-carousel__container {
    position: relative;
    height: 260px !important;
}
.el-carousel__item:nth-child(2n) {
background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
background-color: #d3dce6;
}
.left{
	width: 75%;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	flex-wrap: wrap;
}
.left img{
	width: 100%;
	height: 100%;
	border-radius: 4px;
}
.right{
  width: 23.5%;
}
.swiper{
 	width: 68.5%;
	height: 260px;
	border-radius: 4px;
}
.img_row{
	width: 30%;
	height: 260px;
}
.img_row li{
	width: 100%;
	height: 125px;
	margin-bottom: 10px;
	border-radius: 4px;
	position: relative;
	transition: all ease-in-out 0.5s;
}
.img_row li:hover{
	transform: scale(1.05);
}
.img_row li span{
	position: absolute;
	top: 30%;
	color: white;
	padding: 0 40px;
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	line-height: 24px;
	height: 48px;
}
.card{
	box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 1px rgba(0,0,0,.7);
    border-radius: 3px;
    background: url('../assets/images/car.png') no-repeat top right rgba(42,42,42,1);
    height: 260px;
    overflow: hidden;
    margin-bottom: 20px;
}
.card h2 {
    font-size: 22px;
    margin: 10px;
    color: #89919a;
    font-weight: normal;
    padding-bottom: 10px;
}
.card p {
	font-size: 12px;
	padding: 0 0 0 20px;
	line-height: 28px;
	text-shadow: 0px 1px 2px rgba(0,0,0,.5);
	color: #d0d2d4;
	-webkit-animation: animations2 5s ease-in-out 5s;
	-moz-animation: animations2 5s ease-in-out 5s;
	-o-animation: animations2 5s ease-in-out 5s;
	-ms-animation: animations2 5s ease-in-out 5s;
	animation: animations2 5s ease-in-out 5s;
}
.linkicon{
	display: flex;
	justify-content: space-around;
	margin: 15px;
}
.linkicon li{
	width: 25%;
}
.linkicon li a{
	display: block;
	height: 54px;
    overflow: hidden;
    box-shadow: 0px 1px 0px rgba(255,255,255,.1), inset 0px 1px 1px rgba(0,0,0,.7);
    border-radius: 50%;
	margin: 0 5px;
}
